import React, {useState} from "react";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import './detail.scss';

const Detail = (props) => {

  let [number, setNumber] = useState(1);

  const changeNumber = (e) => {

    if ( e.target.value < 1) {

      setNumber(1);

    }else{
      setNumber(e.target.value);
    }
  }

  let { id } = useParams();

  let content = useSelector((state) => state);

  let price = content.datas[id].price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');

    return (
      <div className="container_wrap">
      <div className="container">
        <div className="row">
          <div className="col-md-6">
            <img src= {require('../d_' + id + '.jpg')} width="100%" alt=" "/>
            <div className="small_images">
              <div className="row">
                <div className="col-4">
                  <img src={require('../detail_0.jpg')} alt=""  width='100%'/>
                </div>
                <div className="col-4">
                  <img src={require('../detail_1.jpg')} alt=""  width='100%'/>
                </div>
                <div className="col-4">
                  <img src={require('../detail_2.jpg')} alt=""  width='100%'/>
                </div>
              </div>
              {/* .row */}
            </div>
          </div>
          {/* .col-md-6 */}
          <div className="col-md-6 detail_text_wrap">
            <div className="detail_text">
              <h4>{'[' + content.datas[id].title + ']'}</h4>
              <p className="content_text">{content.datas[id].content}</p>
              <div className="price_wrap">
                <span></span>
                <span className="big_num">{price}</span>
                <div className="sale_wrap">
                  <span className="sale_price">150000</span>
                  <span className="sale_percent">10%</span>
                </div>
                {/* .sale_wrap */}
              </div>
              {/* .price_wrap */}
              <div className="quan_wrap">
                <span></span>
                <input type='number' value={number} onChange={changeNumber}/>
              </div>
              {/* .quan_wrap */}
              <div className="button_wrap">
                <button></button> 
                <button></button>
              </div>
              {/* .button_wrap */}
            </div> 
            {/* .detail_text */}
          </div>
          {/* .detail_text_wrap */}
        </div>
        {/* .row */}
        <div className="info_text_box_wrap">
          <div className="info_text_wrap">
            <div className="title">
              <h4> </h4>
            </div>
            {/* .title */}
            <div className="product_text">
                <span className="small_title">"  , "</span>
                <span className="small_text">    200     .<br/>, , , , (Cold Meal),  , , BAR  8           .</span>
                <span className="config">  : , /    1</span>
            </div>
            {/* .product_text */}
          </div>
          {/* .info_text_wrap */}
          <div className="info_text_wrap">
            <div className="title">
              <h4></h4>
            </div>
            {/* .title */}
            <div className="product_text">
              <ul className="product_list">
                <li>  : </li>
                <li>  :   MAIN TOWER 1</li>
                <li> : 02-317-7171 ~ 2</li>
                <li> </li>
                <li>[~]  18:00 ~ 21:30 ( 17:30 ~ 21:30)</li>
                <li>[  ]</li>
                <li> 1 11:30 ~ 13:30, 2 14:00 ~ 16:00</li>
                <li>1 17:30 ~ 19:30, 2 20:00 ~ 22:00</li>
                <li>     .(4 , 2 )</li>
                <li>        .</li>
                <li>      .</li>
                <li>        .</li>
                <li>          .</li>
                <li>             .</li>
              </ul>
            </div>
            {/* .product_text */}
          </div>
          {/* .info_text_wrap_01 */}
          <div className="info_text_wrap">
            <div className="title">
              <h4></h4>
            </div>
            {/* .title */}
            <div className="product_text">
              <ul className="product_list">
                <li> : ()  </li>
                <li> ,  (     ) :  93</li>
                <li>     , 100% </li>
                <li>    :   </li>
                <li>     :    /  </li>
                <li>     : 02-759-7700</li>
              </ul>
            </div>
            {/* .product_text */}
          </div>
          {/* .info_text_wrap */}
        </div>
        {/* .info_text_box_wrap */}

      </div>  
      {/* .container */}
    </div>   
    )
}

export default Detail;