import React, {useState,useEffect} from "react";
import { Link } from 'react-router-dom';
import './header.scss';

const Header = (props) => {

    let add = '/lotte_portfolio';

    const [scrollPosition, setScrollPosition] = useState(0);

    const updateScroll = () => {

        setScrollPosition(window.scrollY || document.documentElement.scrollTop);
    }

  
    useEffect(() => {
  
        window.addEventListener('scroll', updateScroll);

    },[]);

    useEffect(() => {

        // .ham_wrap   .none_display  

        let hamButton = document.querySelector('.ham_wrap');

        hamButton.addEventListener('click', function(){

            document.querySelector('.none_display').classList.toggle('ham_menu_wrap');

        });

        let linkButton = document.querySelectorAll('.none_display a');

        for(let i = 0; i < linkButton.length; i++) {

            linkButton[i].addEventListener('click', function(){

            document.querySelector('.none_display').classList.toggle('ham_menu_wrap');

            });
    
        }

        // linkButton.map((data, index) => {

        //     return (
        //         linkButton[index].addEventListener('click', function(){
               
        //         document.querySelector('.none_display').classList.toggle('ham_menu_wrap');
        //     })
        //     )
        // });
        
    },[]);

    const [test, setTest] = useState('');

    return (
        <>
        <div className="header">
            <div className={props.topPage ?( scrollPosition > 150 ? 'header_in_bottom' : 'header_in_top') : 'header_in_bottom'}>
                <div className="logo">
                    <Link to={add} exact>LOTTE HOTEL e-SHOP</Link>
                </div>
                <div className="ham_wrap">
                    <div className="ham">
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                    {/* .ham */}
                </div>
                {/* .ham_wrap */}
                <div className="none_display">
                        <ul>
                            <li>
                                <Link to='/components/drive'></Link>
                            </li>
                            <li>
                                <Link to='/components/store'> </Link>
                            </li>
                            <li>
                                <Link to='/components/food'></Link>
                            </li>
                            <li>
                                <Link to='/components/primium'> </Link>
                            </li>
                            <li>
                                <Link to='/components/life'></Link>
                            </li>
                            <li>
                                <Link to='/components/paper'></Link>
                            </li>
                        </ul>
                    </div>
                    {/* .none_display */}

                <div className="menu">
                    <ul>
                        <li>
                            <Link to ='/components/drive' onClick={() => setTest('drive')} className={test === 'drive' ? 'visit_a' : 'nomal_a'}></Link>
                        </li>
                        <li>
                            <Link to='/components/store' onClick={() => setTest('store')} className={test === 'store' ? 'visit_a' : 'nomal_a'}> </Link>
                        </li>
                        <li>
                            <Link to='/components/food' onClick={() => setTest('food')} className={test === 'food' ? 'visit_a' : 'nomal_a'}></Link>
                        </li>
                        <li>
                            <Link to='/components/primium' onClick={() => setTest('primium')} className={test === 'primium' ? 'visit_a' : 'nomal_a'}> </Link>
                        </li>
                        <li>
                            <Link to='/components/life' onClick={() => setTest('life')} className={test === 'life' ? 'visit_a' : 'nomal_a'}></Link>
                        </li>
                        <li>
                            <Link to='/components/paper' onClick={() => setTest('paper')} className={test === 'paper' ? 'visit_a' : 'nomal_a'}> </Link>
                        </li>
                    </ul>
                </div>
                {/* .menu */}
                <div className="info_menu_wrap">
                    <div className="info_menu">
                        <ul>
                            <li>
                                <Link></Link>
                            </li>
                            <li>
                                <Link><br/></Link>
                            </li>
                            <li>
                                <Link>FAQ</Link>
                            </li>
                            <li>
                                <Link></Link>
                            </li>
                        </ul>
                    </div>
                    {/* info_menu */}
                    <div className="search_box">
                        <input placeholder=" "/>
                    </div>
                </div>
                {/* info_menu_wrap */}
            </div>
            {/* header_in */}
        </div>
        {/* .header */}
        </>
    )
}

export default Header;