import React, {} from "react";
import { useParams } from 'react-router-dom';
import './navlist.scss';
import NavCard from "./nav_card";

const Navlist = (props) => {

    const { id } = useParams();

    const total = props.datas.filter((a) => {
        return a.cate === id ? { ...a } : null;
    })

    let area = '';

switch(id) {
    case 'drive':
        area = '';
        break;
    case 'store':
        area = ' ';
        break;

    case 'food':
        area = '';
        break;

    case 'primium':
        area = ' ';
        break;

    case 'life':
        area = '';
        break;

    case 'paper':
        area = ' ';
        break;

}

    return (
        <div className="container_wrap">
            <div className="container_flex">
                <div className="area_position">
                    <div className="area_wrap">
                        <h4>{area}</h4>
                        <div className="text_wrap">
                            <p> </p>
                            <p> </p>
                            <p> </p>
                            <p> </p>
                        </div>
                    </div>
                </div>
                {/* .area_wrap */}
                <div className="contents_wrap">
                    <h2>{area}</h2>
                        <div className="title">
                        <p className="total"> {total.length}  </p>
                        <select>
                            <option value="a1"></option>
                            <option value="a2"></option>
                            <option value="a3"></option>
                            <option value="a4"></option>
                            <option value="a5"></option>
                        </select>
                    </div>
                    <div className="content">

                        {
                            total.map((data) => {
                                return <NavCard datas={data} key={data.id}/>
                            })
                        }
                    </div>
                    {/* .content */}
                </div>
                {/* .contents_wrap */}
            </div>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/> <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/>


        </div>
    )
}

export default Navlist;